import { useCallback, useEffect, useState } from "react"
import useGetRetailers, { Data } from "../useGetRetailersBySearch";
export type RetailerOption = {
  value: number,
  label: string,
  isDisabled?: boolean,
  isFixed?: boolean,
}
type UseRetailersOptions = (centerId: string, apiURI: string) => {
  isLoading: boolean,
  loadOptions: (inputValue: string, callback: (options: any[]) => void) => void,
  retailers: RetailerOption[],
  nextPage: () => void,
};
const useRetailersOptions: UseRetailersOptions = (centerId, apiURI) => {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [retailers, setRetailers] = useState<any[]>([]);
  const [page, setPage] = useState<number>(1);
  const [limit] = useState<number>(100);
  const [pages, setPages] = useState<number>(0);
  const fetchRetailers = useGetRetailers({ centerId, apiURI });

  const processData = useCallback((data: Data, isLoadingMore?: boolean) => {
    setIsLoading(false);
    let result: any = [];
    if (!data) return result;
    if (!data.isError) {
      result = data.items.map((item: any) => {
        return {
          value: item.id,
          label: `${item.name}${item.location && item.location.length > 1 ? item.location : ''}`,
        }
      })
    } else {
      console.error(`Fetch Retailers error: ${data.error}`)
    }
    return result;
  }, []);

  const loadOptions = useCallback((inputValue: string, callback: (options: RetailerOption[]) => void) => {
    setIsLoading(true);
    const retailersData = fetchRetailers(inputValue);
    retailersData.then(data => {
      const result = processData(data)
      callback(result);
    })
  }, [fetchRetailers, processData])

  useEffect(() => {
    setIsLoading(true);
    const retailersData = fetchRetailers('', page, limit);
    retailersData.then(data => {
      setPages(Math.round(data.count / limit));
      const result = processData(data);
      if (Math.round(data.count / limit) > page) {
        result.push({
          value: -1,
          label: 'Loading more...',
          isDisabled: true,
        })
      }
      setRetailers([...result]);
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fetchRetailers, limit, processData])

  const nextPage = useCallback(() => {
    if (page < pages) {
      setIsLoading(true);
      const retailersData = fetchRetailers('', page + 1, limit);
      retailersData.then(data => {
        setPage(page => page + 1);
        const result: RetailerOption[] = processData(data);
        if (pages > page + 1) {
          result.push({
            value: -1,
            label: 'Loading more...',
            isDisabled: true,
          })
        }
        setRetailers(prev => ([...prev.filter(item => item.value !== -1), ...result]));
      })
    }
  }, [fetchRetailers, limit, page, pages, processData]);


  return {
    isLoading,
    loadOptions,
    retailers,
    nextPage,
  }
}

export default useRetailersOptions;